.product__list {
  display: flex;

  @include vp-1199 {
    flex-direction: column;
    align-items: center;
  }
}

.product__item {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 38px;
  padding: 67px 76px;
  background-color: $color-default-white;
  font-size: 60px;
  font-weight: 700;
  z-index: 3;

  &:not(:last-child) {
    margin-right: 40px;

    @include vp-1365 {
      margin-right: 30px;
    }

    @include vp-1199 {
      margin-right: 0;
    }
  }

  @include vp-1365 {
    padding: 67px 67px;
  }

  @include vp-1199 {
    padding: 70px 118px;
  }

  @include vp-767 {
    margin-bottom: 36px;
    padding: 45px 42px 55px;
  }

  &:hover {
    padding: 61.5px 70px;
    border: 6px solid #003EB7;
    border-image: linear-gradient(#D10A42, #00359F) 30;

    &::before {
      top: 196px;
      left: 72px;
    }
  }

  h3 {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 22px;
    font-weight: 700;
    text-align: center;

    @include vp-767 {
      font-size: 20px;
    }
  }

  p {
    margin-top: 0;
    margin-bottom: 102px;
    font-size: 16px;
    font-weight: 400;
    text-align: center;

    @include vp-767 {
      margin-bottom: 80px;
    }
  }
}

.product__price {
  margin-top: 0;
  text-align: center;

  &::before {
    position: absolute;
    top: 202px;
    left: 78px;
    content: attr(data-price);
    color: $color-100-white;
    font-size: 100px;
    z-index: -1;

    @include vp-1199 {
      top: 206px;
      left: 27%;
    }

    @include vp-767 {
      top: 161px;
      left: 22%;
      font-size: 80px;
    }
  }

  span {
    margin-top: 0;
    color: $color-additional-blue;
    font-size: 60px;
    font-weight: 700;

    @include vp-1199 {
      margin-bottom: 67px;
    }

    @include vp-767 {
      margin-bottom: 55px;
      font-size: 48px;
    }

    mark {
      color: $color-blue;
      background-color: transparent;
    }
  }
}

.product__btn {
  margin-top: 68px;
  border: 2px solid $color-medium-blue;

  @include vp-1199 {
    padding: 25px 40px;
  }

  @include vp-767 {
    padding: 25px 42px;
  }
}

.product__item:hover .product__btn {
  padding: 28px 45px;
  color: $color-default-white;
  border: none;
  background: linear-gradient(111deg, #ED0233 20%, #003EB7 80%);
}

.product__item:hover .product__price::before {
  top: 197px;
  left: 72px;
}
